<template>
  <div>
    <van-pull-refresh
      v-model="isLoading"
      @refresh="onRefresh"
      success-text="刷新成功"
    >
      <van-search
        class="serch1"
        placeholder="点击前往搜索"
        shape="round"
        background="#FF4500"
        style="padding: 10px 5px"
        @click="tosearch"
      />

      <van-swipe :autoplay="3000" indicator-color="red">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image" class="img1" />
        </van-swipe-item>
      </van-swipe>

      <van-grid square :column-num="5" :border="false">
        <van-grid-item
          v-for="value in channel"
          :key="value.id"
          :icon="value.icon"
          :text="value.text"
        >
        </van-grid-item>
      </van-grid>

      <div>
        <van-cell title="新品" is-link value="更多" />
        <div>
          <van-card
            lazy-load
            :title="items.title"
            :desc="items.desc"
            :thumb="items.images"
            :price="items.newPrice | numFilter"
            :origin-price="items.oldPrice"
            v-for="items in newProduct"
            :key="items.id"
          >
            <template #tags>
              <van-tag plain type="primary" style="margin-right: 5px">{{
                items.discount
              }}</van-tag>
              <van-tag plain type="danger">{{ items.activity }}</van-tag>
            </template>
          </van-card>
        </div>
      </div>

      <div>
        <van-cell title="品牌专区" />
        <van-grid :border="false" :column-num="2">
          <van-grid-item v-for="items in brandlist" :key="items.id">
            <van-image lazy-load :src="items.images" style="width: 100%" />
            <div>{{ items.name }}</div>
          </van-grid-item>
        </van-grid>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  data() {
    return {
      images: [],
      channel: [],
      newProduct: [],
      brandlist: [],
      isLoading: false,
    };
  },
  methods: {
    async getinfo() {
      const { data: res } = await this.$http.get("/home");
      this.images = res.list.images;
      this.channel = res.list.channel;
      this.newProduct = res.list.newProduct;
      this.brandlist = res.list.brandlist;
    },
    tosearch() {
      this.$router.push("/search");
    },
    onRefresh() {
      setTimeout(() => {
        location.reload();
        this.isLoading = false;
      }, 1000);
    },
  },

  created() {
    this.getinfo();
  },
  filters: {
    numFilter(value) {
      let tempVal = parseFloat(value).toFixed(3);
      let realVal = tempVal.substring(0, tempVal.length - 1);
      return realVal;
    },
  },
};
</script>
<style lang="less" scoped>
.img1 {
  width: 100%;
  height: 250px;
}
</style>